  cursor: pointer; }


.img-message {
  margin-top: -0.5em;
  margin-bottom: -0.5em; }


















.big-box {
  width: 100%;
  height: 400px;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center; }



.fixed-size {
  width: 100%;
  height: 100px;
  border: 2px solid lightgray;
  background: lightgray;
  color: gray; }

.hoverable:hover {
  border: 2px solid black;
  color: black;
  background: gray; }

.hovered {
  border: 2px solid black;
  color: black;
  background: gray; }



.fixed-h-w {
  height: 100px;
  width: 350px; }

.font-responsive {
  font-size: 16px; }

@media (max-width: 480px) {
  .font-responsive {
    font-size: 9px; } }


span.size-small {
  display: inline-block;
  width: 18em; }

img.size-small {
  display: block;
