span.size-medium {
  display: inline-block;
  width: 30em; }

span.size-custom {
  display: inline-block;
  width: 100px;
  font-size: 0.7em; }

img.size-custom {
  display: block;
  width: 150px;
  height: 100px; }

.font-small {
  font-size: 1em !important; }

